import React from 'react';
import BigButton from '../big-button/big-button';
import Icone from '../icone/icone.component';
import Line from '../line/line.component';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';

type RightOfRow4Type = {
    icone: string;
}
const RightOfRow4 = (props: RightOfRow4Type) => {
    return (
        <div className='flex flex-col items-end'>
            <div className='justify-end flex'>
                <Icone  src={props.icone} />
            </div>
            <Title className='mt-24 mb-6 md:text-left' color='title' fonsize='text-4xl' title='کمپ استارتاپی هوش مصنوعی' />
            <Line color='#e10098' />
            <Paragraph className='mt-6' color='title' fonsize='text-xl' title='کمپ استارتاپی هوش مصنوعی دیجی‌نکست، یک برنامه استارتاپی است که در آن از دانشجویان، متخصصان،‌ و فعالان حوزه‌های تخصصی دعوت می‌شود تا با ‌حمایت دیجی‌نکست اقدام به تیم‌سازی و توسعه محصول خود نمایند. در این برنامه، افراد پذیرفته شده پس از شرکت در یک دوره تیم‌سازی و انتخاب تیم خود وارد فاز توسعه محصول می‌شوند. در طول مدت برنامه تیم‌های استقرار یافته از خدماتی بهره‌مند می‌شوند که می‌تواند در موفقیت آنها به عنوان یک استارتاپ موثر باشد و با استفاده از آنها با سرعت بیشتری به بازار وارد شوند. در صورتی که تخصص شما در زمینه هوش مصنوعی، یادگیری ماشین، یادگیری عمیق، آمار، تحلیل داده، مهندسی داده، داده‌های کلان و یا توسعه دهنده نرم افزار هست، می‌توانید در این برنامه شرکت کنید و استارتاپ تکنولوژی محور خود را با کمک هم‌تیمی‌ها و همراهی دیجی‌نکست بسازید.' />
            <div className='w-72'>
                <BigButton margin='mt-6' title='مشاهده بیشتر' borderColor='#e10098' />
            </div>
        </div>
    );
};

export default RightOfRow4;