import React from 'react';
import icone1 from '../../images/Emoticon/farst-icone1.png';
import icone2 from '../../images/Emoticon/second-icone.png';
import icone3 from '../../images/Emoticon/thre-icone.png';
import icone4 from '../../images/Emoticon/for-icone.png';
import Boxs from '../boxs/boxs.component';
const RowBoxs = () => {
    const data = [
        { icone1: icone1, title: 'سرمایه‌گذاری', paragraph: 'سرمایه‌گذاری و همراهی با استارتاپ‌های متمرکز بر فناوری‌های پیشرفته' },
        { icone1: icone2, title: 'کمپ استارتاپی هوش‌مصنوعی', paragraph: 'تیم‌سازی و ایجاد استارتاپ‌های متمرکز بر فناوری‌های پیشرفته' },
        { icone1: icone3, title: 'آکادمی', paragraph: 'پلتفرم شبکه‌سازی و آموزش هوش مصنوعی' },
        { icone1: icone4, title: 'پژوهش', paragraph: 'حل مسائل صنعت با استفاده از فناوری‌های پیشرفته توسط پژوهشگران' },
    ]
    return (
        <div className='grid grid-cols-1 -mt-36  lg:mt-0 gap-4 lg:gap-16 lg:grid-cols-4'>
            {data.map(item => {
                return (
                    <Boxs icone={item.icone1} title={item.title} paragraph={item.paragraph} />
                )
            })}
        </div>
    );
};

export default RowBoxs;