import React from 'react';
import RightOfRow4 from '../right-of-row4/righofrow4';
import icone2 from '../../images/Emoticon/thre-icone.png';
import icone3 from '../../images/Emoticon/image-row5.png';
import LeftRow5 from '../left-of-row5/left-row5';

const Row5 = () => {
    return (
        <div className='container pr-4 pl-4 mx-auto flex flex-col gap-16 md:flex-row justify-between '>
        <div className='w-full md:w-1/2'>
         <img className='w-full h-full' src={icone3} alt="" />
        </div>
        <div className='w-full md:w-1/2'>
          <LeftRow5 icone={icone2}/>
        </div>
    </div>
    );
};

export default Row5;