import React from 'react';
import Icone from '../icone/icone.component';
import icone1 from '../../images/Emoticon/for-icone.png';
import Title from '../title/title.component';
import Line from '../line/line.component';
import Paragraph from '../paragraph/paragraph';
import LargeCircle from '../Large circle/Largecircle.component';

const Row6 = () => {
    return (
        <div className='container w-full md:w-3/5 mx-auto pl-4 pr-4 m-auto '>
            <div className='flex gap-16 justify-between flex-col md:flex-row '>
                <div className='w-full md:w-2/5'>
                    <Icone src={icone1} />
                </div>
                <div className='w-full w-3/5'>
                    <Title className=' mb-6' color='title' fonsize='text-4xl' title='آکادمی دیجی‌نکست' />
                    <Line color='#E10097' />
                    <Paragraph className='mt-6' color='title' fonsize='text-xl' title='آکادمی دیجی‌نکست با هدف شبکه‌سازی میان افراد مستعد در حوزه تکنولوژی بویژه هوش مصنوعی و برنامه‌نویسی ایجاد شده. در آکادمی دیجی‌نکست دروه‌های آموزشی، کارگاه و ایونت‌هایی برگزار می‌شود که هدف از این فعالیت‌ها آموزش به افرادی است که به این حوزه‌ها علاقه دارند. آکادمی دیجی‌نکست با استفاده از دانش و تجربه متخصصان کسب‌وکارهای موفق، استعدادها را شناسایی و به رهبران موثر آینده تبدیل و از ایده‌ها ثروت می‌سازد.' />
                </div>
            </div>
            <div className='flex justify-center mt-8 gap-8 flex-col md:flex-row'>
                <LargeCircle title='۴۶' paragraph='تعداد ورکشاپ‌ها' />
                <LargeCircle title='۴۵۰۰+' paragraph='تعداد شرکت‌کنندگان' />
            </div>
        </div>
    );
};

export default Row6;