import React from 'react';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';

type BoxsType = {
    title?:string;
    paragraph?:string;
    icone?:string;
}
const Boxs = (props:BoxsType) => {
    return (
        <div className='bg-white boxs-max m-auto lg:m-0 rounded shadow-lg pt-6 pb-6 pr-4 pl-4'>
            <img className='h-12 m-auto' src={props.icone} alt="" />
            <Title className='mt-3' textalign='text-center' fonsize='text-xl' title={props.title} />
            <Paragraph className='paragraph' textalign='text-center' fonsize='text-sm' title={props.paragraph} />
        </div>
    );
};

export default Boxs;