import React from 'react';
import Paragraph from '../paragraph/paragraph';
import icone1 from '../../images/Emoticon/instagram.png';

const ProfileTitle = () => {
    return (
        <div className='flex flex-col  gap-3 justify-between md:flex-row'>
            <div className='flex gap-2 items-center'>
                <img className='h-8' src={icone1} alt="" />
                <Paragraph color='text-black' fonsize='text-xl' title='اینستاگرام دیجی‌نکست' />
            </div>
            <div className='text-left'>
                <button className='text-white pt-1 pb-1 pl-3 pr-3 bg-blue-900 rounded-md'>مشاهده پروفایل</button>
            </div>
        </div>
    );
};

export default ProfileTitle;