import React from 'react';
import ProfileTitle from '../profile-title/profileitle';
import icone1 from '../../images/Emoticon/row7image3.jpg';
import icone2 from '../../images/Emoticon/row7-image.jpg';
import icone3 from '../../images/Emoticon/row7-mage.jpg';
import BigButton from '../big-button/big-button';
import Images from '../images/images.component';

const Row7 = () => {
    return (
        <div className='container mx-auto pl-4 mt-8 pr-4'>
            <ProfileTitle/>
            <Images src1={icone3} src2={icone2} src3={icone1}/>
            <div className='m-auto w-full md:w-3/5'>
            <BigButton margin='mt-4' title='دوره‌های گذشته و فعلی در آکادمی دیجی‌نکست' borderColor='#e10098' />
            </div>
        </div>
    );
};

export default Row7;