import React, { useState } from 'react';
import icone1 from '../../images/Emoticon/New-logo.png';
import icone2 from '../../images/Emoticon/icons8-expand-arrow-24.png';
import icone3 from '../../images/Emoticon/icons8-menu-24.png';
import Paragraph from '../paragraph/paragraph';

const Menu = () => {
    const [menu, setmenu] = useState<boolean>(false);
    const data = [
        { title: 'پرتفولیو' },
        { title: 'مقالات' },
        { title: 'معرفی تیم' },
        { title: 'ارتباط با ما' },
    ]
    const onclick = () => {
        setmenu(!menu);
        console.log(menu);
        
    }
    return (
        <div>
        <div className='pt-4 pb-4 md:pt-8 md:pb-8 shadow-xl gap-6 pr-4 pl-4 md:pr-12 md:pl-12 items-center bg-white flex md:flex-row justify-between'>
            <div className='flex gap-10'>
                <img className='h-6' src={icone1} alt="" />
                <a href='#' className='flex items-center'>
                    <img src={icone2} alt="" />
                    <Paragraph color='title' fonsize='text-lg' title='فارسی' />
                </a>
            </div>
            <div className='hidden md:flex justify-center md:justify-start gap-10'>
                {data.map(item => {
                    return (
                        <a href='#'> <Paragraph color='title' fonsize='text-xl' title={item.title} /></a>
                    )
                })
                }
            </div>
            <img onClick={onclick} className='block md:hidden' src={icone3} alt="" />
        </div>
        {menu===true&&
              <div>
              {data.map(item => {
                  return (
                      <a href='#'> <Paragraph className='mt-4' textalign='text-center' color='title' fonsize='text-xl' title={item.title} /></a>
                  )
              })
              }
          </div>
        }
        </div>
    );
};

export default Menu;