import React from 'react';
import BigButton from '../big-button/big-button';
import Icone from '../icone/icone.component';
import Line from '../line/line.component';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';
type LeftRow5Type = {
    icone: string;
}
const LeftRow5 = (props: LeftRow5Type) => {
    return (
        <div>
            <Icone src={props.icone} />
            <Title className='mt-24 mb-6' color='title' fonsize='text-4xl' title='پژوهش' />
            <Line color='#0097a9' />
            <Paragraph className='mt-6' color='title' fonsize='text-xl' title='واحد پژوهش دیجی‌نکست با هدف رفع چالش‌های آینده دور و نزدیک صنعت با استفاده از تحقیقات و پایان‌نامه‌های دانشگاهیان و دانشجویان مقاطع مختلف بنا نهاده شده است و با تبیین متد نوین پژوهش در کشور، پل ارتباط بین دانشگاهیان و محققان مختلف با صنعت و شرکت‌های فناور از جمله دیجی‌کالا است. اهداف این مرکز عبارتند از :
      1.	ایجاد تاثیر بلندمدت اجتماعی با استفاده از تحقیقات محققان
      2.	ساخت مدل اقتصادی جدیدی مبتنی بر تحقیقات کاربردی
      3.	کاربردی‌سازی تحقیقات در جهت ایجاد فناوری‌های نو در دنیا
       4.	ایجاد بستر مناسب برای انتقال فناوری‌های نوآوارنه به صنعت
       5.	جهت‌دهی به تحقیقات برای رسیدن به نتیجه واقعی (اختراع و کسب‌و‌کار و …)' />
            <div className='w-72'>
                <BigButton margin='mt-6' title='مشاهده بیشتر' borderColor='#0097a9' />
            </div>
        </div>
    );
};

export default LeftRow5;