import React from 'react';
import icone1 from '../../images/Emoticon/arrow.svg';
type BigButtonType = {
    borderColor?: string;
    title?:string;
    margin?:string;
}
const BigButton = (props: BigButtonType) => {
    const className = ('flex justify-center items-center gap-4 border-2 text-2xl colorbutton') + ' ' + (props.margin)
    return (
        <button style={{ borderColor: props.borderColor }} className={className}>
            {props.title}
            <img className='h-6' src={icone1} alt="" />
        </button> 
    );
};

export default BigButton;