import React from 'react';
import Icone from '../icone/icone.component';
import icone1 from '../../images/Emoticon/farst-icone1.png';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';
import Line from '../line/line.component';
import icone2 from '../../images/Emoticon/big-img.jpg';
import icone3 from '../../images/Emoticon/big-img2.png';
import icone4 from '../../images/Emoticon/big-img3.png';
import icone5 from '../../images/Emoticon/small-img.png';
import icone6 from '../../images/Emoticon/small-img2.png';
import icone7 from '../../images/Emoticon/small-img3.png';
import BigButton from '../big-button/big-button';

const Row3 = () => {
    const paragraph = (title?:string) => {
        return (
            <Paragraph color='title' fonsize='text-xl' title={title} />
        )
    }
    return (
        <div className='container pr-4 pl-4 mx-auto'>
            <div className='flex flex-col gap-24 md:flex-row'>
                <div className='md:w-2/5'>
                    <Icone src={icone1} />
                </div>
                <div className='space-y-5'>
                    <Title color='title' fonsize='text-4xl' title='سرمایه‌گذاری' />
                    <Line color='#0097a9' />
                    <div className='space-y-1'>
                        {paragraph('فرآیند سرمایه‌گذاری توسط دیجی‌نکست به شرح زیر است: درخواست کننده رزومه تیم و توضیحات فعالیت خود را به ایمیل investment@diginext.ai ارسال می‌کند و دیجی‌نکست برای بررسی و سرمایه‌گذاری فرایند زیر را طی خواهد کرد:')}
                        {paragraph('1. بررسی اولیه فایل‌های ارسالی و تماس جهت برگزاری جلسه فنی اولیه')}
                        {paragraph('2. ارزیابی فنی و تصمیم‌گیری در کمیته سرمایه‌گذاری')}
                        {paragraph('3. ارزیابی موشکافانه (تکنیکال، مالی، حقوقی و کسب‌وکار)')}
                        {paragraph('4. تصمیم‌گیری درخصوص سرمایه‌گذاری در پروژه مربوطه در جلسه هیئت مدیره' )}
                        {paragraph('5. شروع فرایند مذاکره با درخواست کننده سرمایه')}
                        {paragraph('6. انعقاد قرارداد سرمایه‌گذاری')}
                    </div>
                </div>
            </div>
            <div className='flex flex-col mt-14 justify-between gap-4 md:flex-row'>
                <div className='border border-gray-500 w-full md:w-8/12'>
                    <img className='w-full h-full' src={icone2} />
                </div>
                <div className='space-y-4'>
                    <div className='border border-gray-400'>
                        <img src={icone3} />
                    </div>
                    <div className='border border-gray-400'>
                        <img src={icone4} />
                    </div>
                </div>
            </div>
            <div className='flex flex-col mt-4 gap-4 md:flex-row'>
                <div className='border-gray-400 border'>
                    <img src={icone5} />
                </div>
                <div className='border-gray-400 border'>
                    <img src={icone6} />
                </div>
                <div className='border-gray-400 border'>
                    <img src={icone7} />
                </div>
            </div>
            <div className='w-72 m-auto '>
                <BigButton margin='mt-8' title='مشاهده بیشتر' borderColor='#0097a9' />
            </div>
        </div>
    );
};

export default Row3;