import React from 'react';
import RightOfRow4 from '../right-of-row4/righofrow4';
import icone2 from '../../images/Emoticon/second-icone.png';
import icone3 from '../../images/Emoticon/image-row4.png';
import icone4 from '../../images/Emoticon/row4-image2.png';

const Row4 = () => {
    return (
        <div className='container pr-4 pl-4 mx-auto flex flex-col gap-8 md:gap-16 md:flex-row justify-between '>
            <div className='w-full md:w-1/2'>
                <RightOfRow4 icone={icone2}/>
            </div>
            <div className='w-full md:w-1/2'>
             <img src={icone3} alt="" />
             <img className='mt-6' src={icone4} alt="" />
            </div>
        </div>
    );
};

export default Row4;