import React from 'react';
import Line from '../line/line.component';
import Profile from '../profile/profile.component';
import Title from '../title/title.component';
import icone1 from '../../images/Emoticon/mr1.jpg';
import icone2 from '../../images/Emoticon/mis1.png';
import icone3 from '../../images/Emoticon/mis2.png';
import icone4 from '../../images/Emoticon/mis3.png';
import icone6 from '../../images/Emoticon/msi4.png';
import icone7 from '../../images/Emoticon/mise6.png';
import icone8 from '../../images/Emoticon/mise7.png';
import icone9 from '../../images/Emoticon/mise8.png';
import icone11 from '../../images/Emoticon/mise9.png';
import Paragraph from '../paragraph/paragraph';
const Row9 = () => {
    const data = [
        { src: icone1, title: 'امیر صالحی', paragraph: 'مدیرعامل' },
        { src: icone2, title: 'غزاله خلخالی', paragraph: 'دستیار اجرایی' },
        { src: icone3, title: 'نیایش عبداللهی', paragraph: 'کارشناس امور مالی' },
        { src: icone4, title: 'الهام لطفی', paragraph: 'مدیر سرمایه‌گذاری' },
    ]
    const data1 = [
        { src: icone6, title: 'رقیه بایرام‌زاده', paragraph: 'مدیر تحقیق و توسعه' },
        { src: icone7, title: 'فاطمه رشیدی', paragraph: 'کارشناس روابط عمومی' },
        { src: icone8, title: 'کمند سلیمانی', paragraph: 'کارشناس آکادمی' },
        { src: icone9, title: 'سحر منصوری', paragraph: 'کارشناس سرمایه‌گذاری' },
    ]
    const data2 = [
        { src: icone11, title: 'محمدحسن اسماعیلی', paragraph: 'کارشناس کمپ استارتاپی' },
    ]
    return (
        <div className='container mx-auto pl-4 pr-4'>
            <div className='w-full md:w-6/12 m-auto md:m-0 md:mr-auto'>
                <Title className=' mb-6' color='title' fonsize='text-4xl' title='تیم دیجی‌نکست' />
                <Line color='#0097a9' />
                <Paragraph className='mt-6' color='paragraph-title' fonsize='text-2xl' title='خانواده دیجی‌نکست...' />
            </div>
            <div className='mt-24 flex flex-wrap gap-4 justify-between md:gap-24 md:justify-center'>
                <Profile model={data} />
            </div>
            <div className='flex flex-wrap gap-4 md:gap-24 justify-between md:justify-center'>
                <Profile model={data1} />
            </div>
            <div className='flex flex-wrap gap-4 md:gap-24 justify-between md:justify-center'>
                <Profile model={data2} />
            </div>
        </div>
    );
};

export default Row9;