import React from 'react';
type IconeType = {
    src: string;
    textAlignment?: 'text-left' | 'text-center' | 'text-right' | undefined;
}
const Icone = (props: IconeType) => {
    const className = ('h-16 w-auto') + ' ' + (props.textAlignment)
    return (
        <img className={className} src={props.src} alt="" />
    );
};

export default Icone;