import React from 'react';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';

type ImageType = {
    title?: string;
    paragraph?: string;
    title2?: string;
    paragraph2?: string;
    title3?: string;
    paragraph3?: string;
    src1?: string;
    src2?: string;
    src3?: string;
    className?:string;
}
const Images = (props: ImageType) => {
    return (
        <div className='grid grid-cols-1 md:grid-cols-3 gap-6 mt-6'>
            <div>
                <div className={props.className}>
                    <img className='h-full w-full' src={props.src1} alt="" />
                </div>
                <Title className='mt-2' color='title' fonsize='text-2xl' title={props.title} />
                <Paragraph className='mt-3' color='title' fonsize='text-lg' title={props.paragraph} />
            </div>
            <div>
                <div className={props.className}>
                    <img className='h-full w-full' src={props.src2} alt="" />
                </div>
                <Title className='mt-2' color='title' fonsize='text-2xl' title={props.title2} />
                <Paragraph className='mt-3' color='title' fonsize='text-lg' title={props.paragraph2} />
            </div>
            <div>
                <div className={props.className}>
                    <img className='h-full w-full' src={props.src3} alt="" />
                </div>
                <Title className='mt-2' color='title' fonsize='text-2xl' title={props.title3} />
                <Paragraph className='mt-3' color='title' fonsize='text-lg' title={props.paragraph3} />
            </div>
        </div>
    );
};

export default Images;