import React from 'react';
import ProfileTitle from '../profile-title/profileitle';
const Row2 = () => {
    const tekrar = () => {
        return (
            <div className='w-full border h-40'></div>
        )
    }
    return (
        <div className='container mx-auto pl-4 mt-40 md:mt-16 pr-4'>
            <div>
                <ProfileTitle />
            </div>
            <div className='grid grid-cols-1 justify-items-center md:grid-cols-3 gap-6 mt-7'>
                {tekrar()}
                {tekrar()}
                {tekrar()}
            </div>
        </div>
    );
};

export default Row2;