import React from 'react';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';
import icone1 from '../../images/Emoticon/icone.svg';
import icone2 from '../../images/Emoticon/icone2.svg';
import icone3 from '../../images/Emoticon/social-aparat.svg';
import icone4 from '../../images/Emoticon/social-twitter.svg';
const Footer = () => {

    const ddd = (title?: string) => {
        return (
            <Paragraph textalign='text-left' color='title-footer ' fonsize='text-xl' title={title} />
        )
    }
    const dayaer = (img?: string) => {
        return (
            <div className='rounded-full bg-white h-10 w-10 flex items-center justify-center'>
                <img src={img} alt="" />
            </div>
        )
    }
    return (
        <div className='container mx-auto pl-4 pt-44 pb-20 pr-4 flex flex-col md:flex-row gap-4 md:gap-16 justify-center'>
            <div className='flex items-end flex-col'>
                <div className='bg-white pr-12 md:pr-28 mb-12 pt-2 pb-2'>
                    <Title textalign='text-left' color='big-title-footer' fonsize='text-5xl md:text-6xl' title='ارتباط با ما' />
                </div>
                <div className='flex justify-center gap-6 mb-4 md:mb-12 items-center'>
                    <div>
                        {ddd('تهران، میدان آزادی، اتوبان لشکری،')}
                        {ddd('تهران، میدان آزادی، اتوبان لشکری،')}
                        {ddd('تهران، میدان آزادی، اتوبان لشکری،')}
                    </div>
                    <img src={icone1} alt="" />
                </div>
                <div className='flex justify-center gap-6 items-center'>
                    <Paragraph className='mt-3' color='title-footer ' fonsize='text-xl' title='021-61930000 داخلی 4601' />
                    <img src={icone2} alt="" />
                </div>
            </div>
            <div>
                <div className='flex justify-end md:justify-start gap-4'>
                    {dayaer(icone4)}
                    {dayaer(icone3)}
                    {dayaer(icone4)}
                    {dayaer(icone3)}
                   
                </div>
                <Paragraph className='mt-3 text-left md:text-right' color='title-footer ' fonsize='text-xl' title='شبکه‌های اجتماعی دیجی‌نکست' />
            </div>
        </div>
    );
};

export default Footer;