import React from 'react';
import Paragraph from '../paragraph/paragraph';
import Title from '../title/title.component';

type model = {
    src?: string;
    title?: string;
    paragraph?: string;
}
type ProfileType = {
    model: model[];
}
const Profile = (props: ProfileType) => {
    return (
        <>
            {props.model.map(item => {
                return (
                    <div className='mb-20'>
                        <img className='rounded-full m-auto h-32 w-32 flex' src={item.src} alt="" />
                        <Title textalign='text-center' className='mt-3' color='paragraph-title' fonsize='text-3xl' title={item.title} />
                        <Paragraph textalign='text-center' className='mt-4'  color='title' fonsize='text-xl' title={item.paragraph} />
                    </div>
                )
            })}
        </>

    )
}

export default Profile;